import React, { useEffect, useState } from "react";
import JobService from "../../service/JobService";
import SettlementSubForm from "./SettlementSubForm";
import { InputLabel ,Paper,Grid,Typography,Button} from "@material-ui/core";
import moment from 'moment';
import { Formik, Form } from "formik";
import AppDatePicker from "../../lib/components/datepicker/appDatePicker";
import { ThemeProvider } from "@material-ui/core/styles";
import { createMuiTheme } from "@material-ui/core/styles";

const todayDate=moment(new Date());
const yesterdayDate=moment(new Date()).subtract(1, 'days');

const searchForm = {
  fromDate: yesterdayDate.format('DD/MM/YYYY'),
  toDate: todayDate.format('DD/MM/YYYY'),
 
};


const Theme = createMuiTheme({
  overrides: {
    MuiOutlinedInput: {
      root: {
        // position: "relative",
        // "& $notchedOutline": {
        //   borderColor: "#2EFF22"
        // },
        "&:hover:not($disabled):not($focused):not($error) $notchedOutline": {
          borderColor: "#0E1B3D",
          // Reset on touch devices, it doesn't add specificity
          "@media (hover: none)": {
            borderColor: "#0E1B3D",
          },
        },
        "&$focused $notchedOutline": {
          borderColor: "#0E1B3D",
          borderWidth: 1.5,
        },
      },
    },
  },
});

function Settlement(props) {
  const [settlementList, setSettlementList] = useState([]);
  const [formvalues] = useState(searchForm);


  const onSearch = (values) => {

    JobService.fetchSettlements(values.fromDate,values.toDate)
    .then((response) => {
        if (response.isAxiosError) 
        setSettlementList([]);
        else{
      
          setSettlementList(response.data.dataItems);
        }
      
      })
      .catch(() => {
        console.log("error");
      });
  };


  useEffect(() => {
    console.log("inside load trucks grp");
    let fromDate=yesterdayDate.format('DD/MM/YYYY');
    let todate=todayDate.format('DD/MM/YYYY');

    const loadSettlement = async () => {
      JobService.fetchSettlements(fromDate,todate)
      .then((response) => {
          if (response.status==='fail')  setSettlementList(0);
          else {
            console.log("printing", response);
            setSettlementList(response.data.dataItems);
          }
        })

        .catch(() => {
          console.log("error");
        });
    };

    loadSettlement();
  }, []);

  return (
    <>
      <InputLabel
        style={{ fontSize: "20px", marginTop: "30px", color: "black" }}
      >
        SETTLEMENTS
      </InputLabel>

      <Formik initialValues={formvalues} enableReinitialize>
          {(formik) => {
            
            return (
              <>
                <Form autoComplete="off">
                  <ThemeProvider theme={Theme}>
                    <Grid container spacing={1}>
                      <Grid item xs={2}>
                        <AppDatePicker
                          name={"fromDate"}
                          id={"fromDate"}
                          label={"From Date"}
                          iconColor="#0E1B3D"
                          defaultDate={yesterdayDate}
                        />
                      </Grid>
                      <Grid item xs={2}>
                        <AppDatePicker
                          name={"toDate"}
                          id={"toDate"}
                          label={"To Date"}
                          iconColor="#0E1B3D"
                          defaultDate={todayDate}
                        />
                      </Grid>
                     

                      <Grid item xs={3}>
                        <Button
                          variant="contained"
                          style={{
                            backgroundColor: "#0E1B3D",
                            color: "#FFFFFF",
                            marginTop: "7px",
                          }}
                          onClick={() => onSearch(formik.values)}
                        >
                          Search
                        </Button>
                      </Grid>
                    </Grid>
                  </ThemeProvider>
                </Form>
              </>
            );
          }}
        </Formik>








      {settlementList && settlementList.length>0 ?

      (settlementList.map((job) => (
        <SettlementSubForm
          job={job}
          key={job.referenceNumber}
        ></SettlementSubForm>
      ))): (<Paper elevation={5} style={{ borderRadius: 8, padding: '30px', marginTop: 20, minWidth: '760px', minHeight: '100px', color: '#FF7171' }}>
      <Grid container direction="row" spacing={5}>
        <Grid item sm={12} xs={12}>
          <Typography variant="subtitle1" style={{ fontSize: '16px', textAlign: 'center' }}>
            <b>{'No records found'}</b></Typography>
        </Grid>
      </Grid>
    </Paper>)}
    </>
  );
}

export default React.memo(Settlement);
