import React, { useEffect, useState } from "react";
import { Card } from "@material-ui/core/";
import Link from "@material-ui/core/Link";
import Button from "@material-ui/core/Button";
import Divider from "@material-ui/core/Divider";
import ApplnFileUpload from "../../lib/components/fileupload/ApplnFileUpload";
import { OTHER_POPUP, NO_DIALOG } from "../../lib/common/Constants";
import StartJobPopup from "./StartJobPopup";
import Box from "@material-ui/core/Box";
import { InputLabel } from "@material-ui/core";
import {
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableRow,
  CardContent,
  Typography,
  Paper,
  IconButton,
  Icon,
  Grid,
} from "@material-ui/core";
import MuiTableCell from "@material-ui/core/TableCell";
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import clsx from "clsx";
import { postHttp } from "../../lib/common/HttpService";
import TableHead from "@material-ui/core/TableHead";
import { useHistory } from "react-router-dom";
import JobService from "../../service/JobService";
import { withStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
  leftPane: {
    width: "60%",
  },
  rightPane: {
    width: "40%",
  },
  matCell: {
    fontSize: "14px",
    height: "160px",
    textAlign: "center",
    borderRight: "1px solid grey",
    width: "500px",
  },
  matCell1: {
    fontSize: "14px",
    height: "160px",
    textAlign: "center",
    borderRight: "1px  grey",
    width: "500px",
  },
});



const defaultProps1 = {
  m: 1,
  style: { width: "12rem", height: "6rem" },
  color: "black",
  textAlign: "center",
  align: "right",
  marginTop: "5px",
  border: "2px dashed #ccc",
};



const StyledTableheader = withStyles({
  root: {
    
    width: 100,
  
  },
})(MuiTableCell);

const StyledTableCell = withStyles({
  root: {
 
    width: 100,
  
  },
})(MuiTableCell);

const StyledLabel = withStyles({
  root: {
    fontSize: "12px",
    color: "#777777",
  },
})(InputLabel);

const StyledLabelData = withStyles({
  root: {
    fontSize: "10px",
    color: "#000000",
  },
})(InputLabel);

function SettlementSubForm(props) {
  const [showPopup, setShowPopup] = useState(false);
  const [render, setRender] = useState(0);
  const classes = useStyles();
  const [viewPopup, setViewPopup] = useState(false);
  const [containerList, setContainerList] = useState([]);
  const [job, setJob] = useState(props.job);
  let history = useHistory();
  

  useEffect(() => {}, []);

  const list = (anchor) => (
    <div>
      <br></br>
      <InputLabel
        style={{ fontSize: "20px", color: "grey", marginLeft: "15px" }}
      >
        {" "}
        Container Details
      </InputLabel>
      <br></br>
      <Divider></Divider>
      <br></br>
      <Table size="small" style={{ width: "300px", marginLeft: "12px" }}>
        <TableHead>
         
            <TableRow>
              <StyledTableheader>
                <StyledLabel> Container Number</StyledLabel>
              </StyledTableheader>
              <StyledTableheader>
                <StyledLabel> ISO Code</StyledLabel>
              </StyledTableheader>
              <StyledTableheader>
                <StyledLabel> Pick Up</StyledLabel>
              </StyledTableheader>
              <StyledTableheader>
                <StyledLabel> Token Out</StyledLabel>
              </StyledTableheader>
              <StyledTableheader>
                <StyledLabel> Token Out Date</StyledLabel>
              </StyledTableheader>
              <StyledTableheader>
                <StyledLabel> Token In </StyledLabel>
              </StyledTableheader>
              <StyledTableheader>
                <StyledLabel> Token In Date</StyledLabel>
              </StyledTableheader>
            </TableRow>
        
        </TableHead>
        <TableBody>
          {job.requestContainerList.map((container, ind) => (
            <TableRow>
            
                <StyledTableCell><StyledLabelData>{container.container_number}</StyledLabelData></StyledTableCell>
                <StyledTableCell><StyledLabelData>{container.iso_code}</StyledLabelData></StyledTableCell>
                <StyledTableCell><StyledLabelData>{container.pickupLocation}</StyledLabelData></StyledTableCell>
                <StyledTableCell><StyledLabelData>{container.tokenOut}</StyledLabelData></StyledTableCell>
                <StyledTableCell><StyledLabelData>{container.timeOutSlot}</StyledLabelData></StyledTableCell>
                <StyledTableCell><StyledLabelData>{container.tokenIn}</StyledLabelData></StyledTableCell>
                <StyledTableCell><StyledLabelData>{container.timeInSlot}</StyledLabelData></StyledTableCell>
              
            
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );

  const openPopup = () => {
    setShowPopup(true);
    // setRender(render + 1);
  };

  const handleClose = () => {
    setShowPopup(false);
  };

  const onFileDownload = (job) => {
    JobService.fetchInvoice(job.referenceNumber)
      .then((response) => {
        if (response.isAxiosError) throw new Error(response.status);
        else {
          const linkSource = `data:${response.data.dataItems[0].filetype};base64,${response.data.dataItems[0].fileContent}`;
          const downloadLink = document.createElement("a");

          downloadLink.href = linkSource;
          downloadLink.download = response.data.dataItems[0].fileName;
          downloadLink.target = "_blank";
          // alert(downloadLink);
          downloadLink.click();
        }
      })

      .catch(() => {
        console.log("error");
      });
  };

  return (
    <>
      <Card style={{ width: "1100px", height: "230px", marginTop: "20px" }}>
        <Table>
          <TableBody>
            <TableRow>
              <TableCell>
                <Grid item xs={10}>
                  <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                    Order Number#
                  </InputLabel>
                </Grid>
                <Grid item xs={10}>
                  <InputLabel style={{ fontSize: "13px", color: "black" }}>
                    {job.referenceNumber}
                  </InputLabel>
                </Grid>
              </TableCell>

              <TableCell>
                <Grid item xs={10}>
                  <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                    Completed On
                  </InputLabel>
                </Grid>
                <Grid item xs={10}>
                  <InputLabel style={{ fontSize: "13px", color: "black" }}>
                    {job.completedDate}
                  </InputLabel>
                </Grid>
              </TableCell>

              <TableCell>
                <Grid item xs={10}>
                  <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                    Number of containers
                  </InputLabel>
                </Grid>
                <Grid item xs={10}>
                  <Link
                    style={{
                      color: "red",
                      position: "relative",
                      marginTop: "1",
                    }}
                    component="button"
                    variant="body2"
                    onClick={() => {
                      setViewPopup(true);
                      setContainerList(job.requestContainerList);
                    }}
                  >
                    {job.noOfContainers}
                  </Link>
                </Grid>
                <SwipeableDrawer
                  anchor="right"
                  open={viewPopup}
                  onClose={() => {
                    setViewPopup(false);
                  }}
                  onOpen={() => {
                    setViewPopup(true);
                  }}
                >
                  {list("right", containerList)}
                </SwipeableDrawer>
              </TableCell>
              <TableCell>
                <Grid item xs={10}>
                  <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                    Contact name
                  </InputLabel>
                </Grid>
                <Grid item xs={10}>
                  <InputLabel style={{ fontSize: "13px", color: "black" }}>
                    {job.driverName}
                  </InputLabel>
                </Grid>
              </TableCell>

              <TableCell>
                <Grid item xs={10}>
                  <InputLabel style={{ fontSize: "13px", color: "grey" }}>
                    Contact Number
                  </InputLabel>
                </Grid>
                <Grid item xs={10}>
                  <InputLabel style={{ fontSize: "13px", color: "black" }}>
                    {job.driverContactNo}
                  </InputLabel>
                </Grid>
              </TableCell>

              <TableCell>
                {job.settlementStatus === "Settlement" && (
                  <Button
                    variant="contained"
                    color="primary"
                    style={{
                      textTransform: "capitalize",
                      backgroundColor: "#3C66D3",
                    }}
                    onClick={() => {
                      history.push("/settlementSummary", { settlement: job });
                    }}
                  >
                    {job.settlementStatus}
                  </Button>
                )}
                {job.settlementStatus === "Invoice Submitted" && (
                  <Grid container spacing={1} alignItems="flex-end">
                    <Grid item>
                      <img
                        src="./document.svg"
                        onClick={() => onFileDownload(job)}
                      />
                    </Grid>
                    <Grid item>
                      <Button
                        variant="contained"
                        color="primary"
                        style={{
                          textTransform: "capitalize",
                          backgroundColor: "#EB9743",
                        }}
                      >
                        {job.settlementStatus}
                      </Button>
                    </Grid>
                  </Grid>
                )}
                {job.settlementStatus === "Invoice Approved" && (
                  <Grid container spacing={1} alignItems="flex-end">
                    <Grid item>
                      <img src="./document.svg" />
                    </Grid>
                    <Grid item>
                      <Button
                        variant="contained"
                        color="primary"
                        style={{
                          textTransform: "capitalize",
                          backgroundColor: "#EB9743",
                        }}
                      >
                        {job.settlementStatus}
                      </Button>
                    </Grid>
                  </Grid>
                )}
                {job.settlementStatus === "Amount Paid" && (
                  <Grid container spacing={1} alignItems="flex-end">
                    <Grid item>
                      <img
                        src="./document.svg"
                        onClick={() => onFileDownload(job)}
                      />
                    </Grid>
                    <Grid item>
                      <Button
                        variant="contained"
                        color="primary"
                        style={{
                          textTransform: "capitalize",
                          backgroundColor: "#63BB7A",
                        }}
                      >
                        {job.settlementStatus}
                      </Button>
                    </Grid>
                  </Grid>
                )}

                {job.settlementStatus === "Invoice Rejected" && (
                  <Grid container spacing={1} alignItems="flex-end">
                    <Grid item>
                      <Button
                        variant="contained"
                        color="primary"
                        style={{
                          textTransform: "capitalize",
                          backgroundColor: "red",
                        }}
                        onClick={() => {
                          history.push("/settlementSummary", {
                            settlement: job,
                          });
                        }}
                      >
                        {job.settlementStatus}
                      </Button>
                    </Grid>
                  </Grid>
                )}
              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
        <TableRow>
          <TableCell className={classes.matCell}>
            <div className="row">
              <Grid container>
                <Grid item xs={3}></Grid>
                <Grid item style={{ marginTop: "40px", marginRight: "-17px" }}>
                  <img src="./location-pin.svg" />
                </Grid>
                <Grid item xs={7}>
                  <Box {...defaultProps1}>
                    {/* {contactArr.map((option, i) => {
                    return (
                      
                      <InputLabel style={{ fontSize: "13px", color: "black" }}>
                        {option},
                      </InputLabel>
                    );
                  })} */}

                    <Grid container style={{ marginTop: "5px" }}>
                      {" "}
                      {job.contactDetails}
                    </Grid>
                  </Box>
                </Grid>
              </Grid>
            </div>

            <Divider orientation="vertical" flexItem />
          </TableCell>
          <TableCell className={classes.matCell1}>
            <Box
              style={{
                border: "2px dashed #ccc",
                height: "90px",
                width: "500px",
                marginBottom: "25px",
              }}
            >
              <div className="row">
                <Grid item style={{ marginTop: "30px", marginRight: "-8px" }}>
                  <img src="./truck.svg" />
                </Grid>
                <Grid item xs={3}></Grid>
                <Grid item>
                  <Box
                    style={{
                      height: "30px",
                      width: "200px",
                      border: "2px solid #ccc",
                      marginTop: "30px",
                      marginLeft: "30px",
                    }}
                  >
                    {job.vehicleRegNo}
                  </Box>
                </Grid>
                {/* <Box
                  style={{
                    height: "30px",
                    width: "200px",
                    border: "2px solid #ccc",
                    marginTop: "30px",
                    marginLeft: "30px",
                  }}
                >
                  {job.vehicleType}
                </Box> */}
              </div>
            </Box>
          </TableCell>
        </TableRow>
      </Card>

      <SwipeableDrawer
        anchor="right"
        open={viewPopup}
        onClose={() => {
          setViewPopup(false);
        }}
        onOpen={() => {
          setViewPopup(true);
        }}
      >
        {list()}
      </SwipeableDrawer>
    </>
  );
}

export default React.memo(SettlementSubForm);
